import activeColor from './activeColor';

export default class Bucket {
  constructor() {
    this.colorLayer = null;
    this.startR = null;
    this.startG = null;
    this.startB = null;
    this.alfa = null;
    this.fillColorR = null;
    this.fillColorG = null;
    this.fillColorB = null;
  }

  dataForBucket(x, y, pixelData, ImageData, tool) {
    const fillColor = activeColor();
    this.startR = pixelData[0];
    this.startG = pixelData[1];
    this.startB = pixelData[2];
    this.alfa = pixelData[3];
    this.tool = tool;
    this.fillColorR = +fillColor[0];
    this.fillColorG = +fillColor[1];
    this.fillColorB = +fillColor[2];
    this.colorLayer = ImageData;
    if (this.tool === 'paint-bucket') {
      this.bucket(x, y);
    } else {
      this.bucketAllPixels(ImageData);
    }
  }

  bucket(startX, startY) {
    const mainCanvas = document.getElementById('main-canvas');
    const ctx = mainCanvas.getContext('2d');
    let pixelStack = [[startX, startY]];

    while (pixelStack.length) {
      let newPos, x, y, pixelPos, reachLeft, reachRight;
      newPos = pixelStack.pop();
      x = newPos[0];
      y = newPos[1];
      pixelPos = (y * mainCanvas.width + x) * 4;

      while (y-- >= 0 && this.matchStartColor(pixelPos)) {
        pixelPos -= mainCanvas.width * 4;
      }

      pixelPos += mainCanvas.width * 4;
      ++y;
      reachLeft = false;
      reachRight = false;

      while (y++ < mainCanvas.width - 1 && this.matchStartColor(pixelPos)) {
        this.colorPixel(pixelPos);
        if (x > 0) {
          if (this.matchStartColor(pixelPos - 4)) {
            if (!reachLeft) {
              pixelStack.push([x - 1, y]);
              reachLeft = true;
            }
          }
          else if (reachLeft) {
            reachLeft = false;
          }
        }

        if (x < mainCanvas.width - 1) {
          if (this.matchStartColor(pixelPos + 4)) {
            if (!reachRight) {
              pixelStack.push([x + 1, y]);
              reachRight = true;
            }
          } else if (reachRight) {
            reachRight = false;
          }
        }

        pixelPos += mainCanvas.width * 4;
      }
    }
    ctx.putImageData(this.colorLayer, 0, 0);
  }

  bucketAllPixels(imageData) {
    const mainCanvas = document.querySelector('#main-canvas');
    const ctx = mainCanvas.getContext('2d');
    const activeFrame = document.querySelector('.framebar-active').getAttribute('data-frame-number');
    const frameCanvas = document.querySelectorAll('.framebar-canvas')[activeFrame];
    const frameCanvasCtx = frameCanvas.getContext('2d');
    const ratioX = mainCanvas.width / frameCanvas.width;
    const ratioY = mainCanvas.height / frameCanvas.height;
    let x, y, x1, y1;

    for (let i = 0; i < imageData.data.length; i += 4) {
      x = Math.floor((i / 4) % mainCanvas.width);
      y = Math.floor((i / 4) / mainCanvas.width);
      x1 = Math.floor((i / 4) % mainCanvas.width / ratioX);
      y1 = Math.floor((i / 4) / mainCanvas.width / ratioY);
      this.startR = imageData.data[i];
      this.startG = imageData.data[i + 1];
      this.startB = imageData.data[i + 2];
      this.alfa = imageData.data[i + 3];
      if (this.startR !== 0 || this.startG !== 0 || this.startB !== 0 || this.alfa !== 0) {
        ctx.beginPath();
        ctx.fillStyle = `rgba(${this.startR}, ${this.startG},${this.startB},${this.alfa})`;
        ctx.fillRect(x, y, 1, 1);
        frameCanvasCtx.beginPath();
        frameCanvasCtx.fillStyle = `rgba(${this.startR}, ${this.startG},${this.startB},${this.alfa})`;
        frameCanvasCtx.fillRect(x1, y1, 1, 1);
      } else {
        this.startR = this.fillColorR;
        this.startG = this.fillColorG;
        this.startB = this.fillColorB;
        this.alfa = 255;
        ctx.beginPath();
        ctx.fillStyle = `rgba(${this.startR}, ${this.startG}, ${this.startB}, ${this.alfa})`;
        ctx.fillRect(x, y, 1, 1);
        frameCanvasCtx.beginPath();
        frameCanvasCtx.fillStyle = `rgba(${this.startR}, ${this.startG}, ${this.startB}, ${this.alfa})`;
        frameCanvasCtx.fillRect(x1, y1, 1, 1);
      }
    }
  }

  matchStartColor(pixelPos) {
    let r, g, b, a;
    r = this.colorLayer.data[pixelPos];
    g = this.colorLayer.data[pixelPos + 1];
    b = this.colorLayer.data[pixelPos + 2];
    a = this.colorLayer.data[pixelPos + 3];
    return (r === this.startR && g === this.startG && b === this.startB && a === this.alfa);
  }

  colorPixel(pixelPos) {
    this.colorLayer.data[pixelPos] = this.fillColorR;
    this.colorLayer.data[pixelPos + 1] = this.fillColorG;
    this.colorLayer.data[pixelPos + 2] = this.fillColorB;
    this.colorLayer.data[pixelPos + 3] = 255;
  }
}
