import activeColor from './activeColor';

export default function dithering(x, y, widthOfPixel) {
  const mainCanvas = document.getElementById('main-canvas');
  const ctx = mainCanvas.getContext('2d');
  const [r, g, b] = activeColor();
  const a = 255;
  const length = x / widthOfPixel;
  const high = y / widthOfPixel;

  ctx.fillStyle = `rgba(${r}, ${g}, ${b}, ${a})`;
  if (length % 2 === 0 && high % 2 === 0) {
    ctx.fillRect(x, y, widthOfPixel, widthOfPixel);
  } else if (length % 2 === 1 && high % 2 === 1) {
    ctx.fillRect(x, y, widthOfPixel, widthOfPixel);
  }
}
