export default function colorPicker(x, y) {
  const mainCanvas = document.getElementById('main-canvas');
  const ctx = mainCanvas.getContext('2d');
  const primaryColor = document.querySelector('.primary-color');
  const src = ctx.getImageData(x, y, 1, 1);
  const [r, g, b, alfa] = src.data;
  if (r === 0 && g === 0 && b === 0 && alfa === 0) {
    return;
  } else {
    primaryColor.style.backgroundColor = `rgba(${r}, ${g}, ${b}, ${alfa})`;
  }
}
