import handlers from './handlers';

export default function canvasSize() {
  const canvasSize = document.querySelector('.canvas-size');
  const mainCanvas = document.querySelector('#main-canvas');
  const size = document.querySelector('.size');
  const pixel = document.querySelector('.pixel');
  let activeResolution = document.querySelector('.active-resolution');
  let widthOfPixel;
  let dimension;
  canvasSize.addEventListener('click', (e) => {
    if (e.target.classList.contains('resolution')) {
      activeResolution.classList.remove('active-resolution');
      e.target.classList.add('active-resolution');
      dimension = e.target.getAttribute('data-size');
      widthOfPixel = mainCanvas.width / +e.target.getAttribute('data-size');
      pixel.style.width = `${widthOfPixel}px`;
      pixel.style.height = `${widthOfPixel}px`;
      size.innerHTML = `[${dimension}x${dimension}]`;
      activeResolution = e.target;
      handlers();
    }
  });
}
