import GIF from './gif';
import spawnCanvas from './spawnCanvas';
import transformBg from './transformBg';

export default class Download {
  png() {
    const activeCanvas = document.querySelector('.framebar-active').firstElementChild;
    const inpTitle = document.querySelector('.inp-format-title');
    let frame = spawnCanvas(activeCanvas);
    let dataURL = frame.toDataURL('image/png', 1.0);
    let link = document.createElement('a');
		document.body.appendChild(link);
		link.href = dataURL;
  	link.download = `${inpTitle.value}.png`;
    link.click();
    inpTitle.value = '';
  	document.body.removeChild(link);
  }

  jpeg() {
    const inpTitle =  document.querySelector('.inp-format-title');
    const activeCanvas = document.querySelector('.framebar-active').firstElementChild;
    let frame = spawnCanvas(activeCanvas);
    frame = transformBg(frame);
    frame.toBlob(function(blob) {
      let url = URL.createObjectURL(blob);
      let link = document.createElement('a');
      document.body.appendChild(link);
      link.href = url;
      link.download = `${inpTitle.value}.jpeg`;
      link.click();
      inpTitle.value = '';
      document.body.removeChild(link);
    }, 'image/jpeg', 1.0);
  }

  gif() {
    const frames = document.querySelectorAll('.framebar-canvas');
    const fps = document.querySelector('.amount-of-FPS').innerHTML;
    const inpTitle = document.querySelector('.inp-format-title');
    let gif = new GIF({
      workers: 2,
      quality: 10,
      workerScript: './gif.worker.js'
    });
    frames.forEach(frame => {
      let newFrame = spawnCanvas(frame);
      newFrame = transformBg(newFrame);
      gif.addFrame(newFrame, {delay: 1000 / +fps})
    });
    gif.on('finished', function(blob) {
      let url = URL.createObjectURL(blob);
      let link = document.createElement('a');
      document.body.appendChild(link);
      link.href = url;
      link.download = `${inpTitle.value}.gif`;
      link.click();
      inpTitle.value = '';
      document.body.removeChild(link);
    });

    gif.render();
  }

}
