export default function setActiveCursor(key, prevTool) {
  const mainRectangle = document.querySelector('.main-rectangle');
  const tool = document.querySelector(`[data-tool=${key}]`);
  const id = tool.firstElementChild.id;
  const prevCursor = `${prevTool.firstElementChild.id}-cursor`;
  const activeCursor = `${id}-cursor`;

  prevTool.classList.remove('tool-active');
  tool.classList.add('tool-active');
  mainRectangle.classList.remove(prevCursor);
  mainRectangle.classList.add(activeCursor);
}
