export default function changeShortcut() {
  const toolCommands = document.querySelector('.tools-shortcuts__nav');
  const tools = document.querySelectorAll('.tool');
  // let shortcuts = document.querySelectorAll('.shortcut');
  let changeShortcutName;
  let timeout;
  const arrayOfToolsCommand = Array.from(tools).map((tool) => {
    return tool.getAttribute('data-tool');
  });
  toolCommands.addEventListener('click', (e) => {
    if (e.target.classList.contains('shortcut')) {
      clearInterval(timeout);
      if (changeShortcutName !== undefined) {
        changeShortcutName.classList.remove('shortcut-change');
        changeShortcutName.classList.remove('active-shortcut');
      }
      if (changeShortcutName === e.target) {
        clearInterval(timeout);
      } else {
        timeout = setInterval(() => {
          e.target.classList.toggle('shortcut-change');
        }, 500);
        e.target.classList.add('active-shortcut');
      }
      changeShortcutName = e.target;
    }
  });

  document.addEventListener('keyup', (e) => {
    if (document.querySelector('.active-shortcut') && arrayOfToolsCommand.includes(e.key.toLowerCase())) {
      const commands = document.querySelectorAll('.shortcut');
      for (let i = 0; i < commands.length; i += 1) {
        if (commands[i].innerText === e.key.toUpperCase()) {
          commands[i].innerHTML = '??';
        }
      }
    }
  });
}
