import AppView from './view';
import render from '../../../components/frames-list/render';

export default function handlers() {
  const mainCanvasWrapper = document.querySelector('.wrapper-canvas');
  const mainCanvas = document.getElementById('main-canvas');
  const mainCanvasCtx = mainCanvas.getContext('2d');
  const pixel = document.querySelector('.pixel');
  const coordinates = document.querySelector('.coord');
  const widthOfCanvas = mainCanvas.width;
  let resolution = document.querySelector('.active-resolution').getAttribute('data-size');
  let step = +resolution;
  let widthOfPixel = widthOfCanvas / step;
  let x, y, x1, y1, coordX, coordY, absoluteX, absoluteY, staticX, staticY;
  let dataOfPixels = {
    x: x,
    y: y,
    x1: x1,
    y1: y1,
    absoluteX: absoluteX,
    absoluteY: absoluteY,
    staticX: staticX,
    staticY: staticY,
    tool: null,
    pixelData: null,
    canvasData: null,
    strokeData: null,
  };

  const view = new AppView();
  let isDown = false;
  pixel.style.width = `${widthOfPixel}px`;
  pixel.style.height = `${widthOfPixel}px`;

  mainCanvasWrapper.addEventListener('mousedown', (e) => {
    const activeElem = document.querySelector('.tool-active');
    resolution = document.querySelector('.active-resolution').getAttribute('data-size');
    step = +resolution;
    widthOfPixel = widthOfCanvas / step;
    isDown = true;
    pixel.classList.remove('active-pixel');
    x = Math.floor(e.layerX /  widthOfPixel) *  widthOfPixel;
    y = Math.floor(e.layerY /  widthOfPixel) *  widthOfPixel;
    staticX = Math.floor(e.layerX /  widthOfPixel) *  widthOfPixel;
    staticY = Math.floor(e.layerY /  widthOfPixel) *  widthOfPixel;
    dataOfPixels.x = x;
    dataOfPixels.y = y;
    dataOfPixels.staticX = staticX;
    dataOfPixels.staticY = staticY;
    dataOfPixels.pixelData = mainCanvasCtx.getImageData(x, y, widthOfPixel, widthOfPixel);
    dataOfPixels.canvasData = mainCanvasCtx.getImageData(0, 0, mainCanvas.width, mainCanvas.height);
    dataOfPixels.strokeData = [];
    dataOfPixels.tool = activeElem.firstElementChild.id;
    dataOfPixels.event = 'mousedown';
    view.render(dataOfPixels);
  });
  mainCanvas.addEventListener('mouseleave', () => {
    pixel.classList.remove('active-pixel');
  });
  mainCanvasWrapper.addEventListener('mousemove', (e) => {
    if (!e.target.classList.contains('pixel')) {
      x1 = Math.floor(e.layerX /  widthOfPixel) *  widthOfPixel;
      y1 = Math.floor(e.layerY /  widthOfPixel) *  widthOfPixel;
      coordX = x1 / widthOfPixel;
      coordY = y1 / widthOfPixel;
      absoluteX = e.layerX;
      absoluteY = e.layerY;
      coordinates.innerHTML = `${coordX}:${coordY}`;
    }

    if (e.target.classList.contains('pixel')) {
      absoluteX += e.layerX;
      absoluteY += e.layerY;
    }

    if(isDown) {
      pixel.classList.remove('active-pixel');
      dataOfPixels.x1 = x1;
      dataOfPixels.y1 = y1;
      dataOfPixels.absoluteX = absoluteX;
      dataOfPixels.absoluteY = absoluteY;
      dataOfPixels.event = 'mousemove';
      dataOfPixels.pixelData = mainCanvasCtx.getImageData(x1, y1, widthOfPixel, widthOfPixel);
      view.render(dataOfPixels);
      x = x1;
      y = y1;
      dataOfPixels.x = x1;
      dataOfPixels.y = y1;
    }
    else {
      pixel.classList.add('active-pixel');
      pixel.style.top = `${y1}px`;
      pixel.style.left = `${x1}px`;
    }
  });
  mainCanvasWrapper.addEventListener('mouseup', () => {
    isDown = false;
    const activeFrame = document.querySelector('.framebar-active').getAttribute('data-frame-number');
    const frameCanvas = document.querySelectorAll('.framebar-canvas')[activeFrame];
    render(mainCanvas, frameCanvas);
    pixel.classList.add('active-pixel');
  });
}
