export default function transform() {
  const transformTool = document.getElementById('transform');
  transformTool.addEventListener('click', () => {
    const mainCanvas = document.getElementById('main-canvas');
    const mainCanvasCtx = mainCanvas.getContext('2d');
    const mainCanvasSrc = mainCanvasCtx.getImageData(0, 0, mainCanvas.width, mainCanvas.height);
    let r, g, b, alfa, x, y, mirrorX;
    for (let i = 0; i < mainCanvasSrc.data.length; i += 4) {
      x = Math.floor((i / 4) % mainCanvas.width);
      y = Math.floor((i / 4) / mainCanvas.height);
      r = mainCanvasSrc.data[i];
      g = mainCanvasSrc.data[i + 1];
      b = mainCanvasSrc.data[i + 2];
      alfa = mainCanvasSrc.data[i + 3];
      if (r === 0 && g === 0 && b === 0 && alfa === 0) {
        continue;
      } else {
        mirrorX = mainCanvas.width - x - 1;
        mainCanvasCtx.beginPath();
        mainCanvasCtx.fillStyle = `rgba(${r}, ${g}, ${b}, ${alfa})`;
        mainCanvasCtx.clearRect(x, y, 1, 1);
        mainCanvasCtx.fillRect(mirrorX, y, 1, 1);
      }
    }
  });
}
