import activeCursor from './activeCursor';

export default function selectTool() {
  const toolList = document.querySelector('.toolbar__nav');
  let activeElem;
  toolList.addEventListener('click', (e) => {
    activeElem = document.querySelector('.tool-active');
    const { target } = e;
    if (target.classList.contains('icon')) {
      activeElem.classList.remove('tool-active');
      target.parentNode.classList.add('tool-active');
      activeElem = target.parentNode;
      activeCursor(target);
    }
  });
}
