import deleteFrame from './deleteFrame';
import dublicateFrame from './dublicateFrame';
import render from './render';

export default function chooseOption(frameObj) {
  let frameCanvas = document.querySelector('.framebar-active').firstElementChild;
  let mainCanvas = document.getElementById('main-canvas');
  let wrapperAddFrame = document.querySelector('.framebar__nav');
  wrapperAddFrame.addEventListener('click', (e) => {
    let { target } = e;
    if (target.parentNode.classList.contains('framebar-window')) {
      const frameCanvasWrapper = document.querySelectorAll('.framebar-window');
      Array.from(frameCanvasWrapper).map((frame) => {
        if (frame.classList.contains('framebar-active')) {
          frame.classList.remove('framebar-active');
        }
      });
      const makeActiveFrame = e.target.parentNode.getAttribute('data-frame-number');
      frameCanvasWrapper[makeActiveFrame].classList.add('framebar-active');

      // FOR RENDERING ON MAIN CANVAS
      frameCanvas = frameCanvasWrapper[makeActiveFrame].firstElementChild;
      mainCanvas.getContext('2d').clearRect(0, 0, mainCanvas.width, mainCanvas.height);
      render(frameCanvas, mainCanvas);
    }
    if (target.classList.contains('delete') || target.classList.contains('dublicate')) {
      let option = target;
      while (!target.classList.contains('framebar-window')) {
        target = target.parentNode;
      }
      mainCanvas.getContext('2d').clearRect(0, 0, mainCanvas.width, mainCanvas.height);
      if (option.classList.contains('delete')) {
        deleteFrame(+target.getAttribute('data-frame-number'), frameObj);
      } else if (option.classList.contains('dublicate')) {
        dublicateFrame(target, +target.getAttribute('data-frame-number'), frameObj);
      }
    }
  });
}
