export default function transformBg(frame) {
  const frameCtx = frame.getContext('2d');
  const src = frameCtx.getImageData(0, 0, frame.width, frame.height);
  let r, g, b, alfa, x, y;
  for (let i = 0; i < src.data.length; i += 4) {
    x = Math.floor((i / 4) % frame.width);
    y = Math.floor((i / 4) / frame.height);
    r = src.data[i];
    g = src.data[i + 1];
    b = src.data[i + 2];
    alfa = src.data[i + 3];
    if (r == 0 && g == 0 && b == 0 && alfa == 0) {
      r = 255;
      g = 255;
      b = 255;
      alfa = 255;
      frameCtx.beginPath();
      frameCtx.fillStyle = `rgba(${r}, ${g}, ${b}, ${alfa})`;
      frameCtx.fillRect(x, y, 1, 1);
    }
  }
  return frame;
}
