import Download from '../../screens/export/download';

export default function preferences () {
  const preferences = document.querySelector('.preferences');
  const preferencesNav = document.querySelector('.preferences-nav');
  const exportPopup = document.querySelector('.popup-preferences__export');
  let activeFormat = document.querySelector('.active-format');
  let activeSize = document.querySelector('.export-size-active');
  let format = activeFormat.getAttribute('data-format');
  const download = new Download();

  preferences.addEventListener('click', (e) => {
    const { target } = e;
    if (target.classList.contains('export')) {
      preferencesNav.classList.toggle('active-preferences-nav');
      exportPopup.classList.toggle('active-popup-export');
    }
    if (target.classList.contains('format')) {
      activeFormat.classList.remove('active-format');
      target.classList.add('active-format');
      activeFormat = target;
      format = activeFormat.getAttribute('data-format');
    }
    if (target.classList.contains('export-size')) {
      activeSize.classList.remove('export-size-active');
      target.classList.add('export-size-active');
      activeSize = target;
    }
    if (target.classList.contains('export-download')) {
      download[format]();
    }
  });
}
