import render from '../../components/frames-list/render';

export default function preview() {
  const rangeOfFPS = document.querySelector('.range');
  let timeout;
  let counterForFrames = 0;

  function startAnimation(fps) {
    const animateCanvas = document.getElementById('animate-canvas');
    const animateCanvasCtx = animateCanvas.getContext('2d');
    const frames = document.querySelectorAll('.framebar-canvas');

    if (counterForFrames === frames.length) {
      counterForFrames = 0;
    }
    const frame = frames[counterForFrames % frames.length];
    counterForFrames += 1;
    animateCanvasCtx.clearRect(0, 0, animateCanvas.width, animateCanvas.height);
    if (fps === 0) {
      render(frame, animateCanvas);
    } else {
      render(frame, animateCanvas);
      timeout = setTimeout(() => {
        startAnimation(fps);
      }, 1000 / fps);
    }
  }
  rangeOfFPS.addEventListener('input', () => {
    const fps = rangeOfFPS.value;
    const amountOfFPS = document.querySelector('.amount-of-FPS');
    amountOfFPS.innerHTML = fps;
    clearTimeout(timeout);
    startAnimation(+fps);
  });
}
