import setActiveCursor from './setActiveCursor';

export default function keyboard() {
  const keyboardPopup = document.querySelector('.keyboard');
  const closePopup = document.querySelector('.shortcuts-close');

  keyboardPopup.addEventListener('click', () => {
    document.querySelector('.popup-shortcuts').classList.toggle('popup-shortcuts__active');
    if (document.querySelector('.active-shortcut')) {
      document.querySelector('.active-shortcut').classList.remove('active-shortcut');
    }
  });

  closePopup.addEventListener('click', () => {
    document.querySelector('.popup-shortcuts').classList.remove('popup-shortcuts__active');
    if (document.querySelector('.active-shortcut')) {
      document.querySelector('.active-shortcut').classList.remove('active-shortcut');
    }
  });

  document.addEventListener('keydown', (e) => {
    const activeItem = document.querySelector('.tool-active');
    const exportPopup = document.querySelector('.popup-preferences__export');
    const keycodes = ['p', 'm', 'b', 'c', 'e', 's', 'l', 'd', 't'];
    if (keycodes.includes(e.key.toLowerCase()) && !exportPopup.classList.contains('active-popup-export')) {
      activeItem.classList.remove('tool-active');
      setActiveCursor(e.key.toLowerCase(), activeItem);
    }
  });
}
