export default function lighten(x, y, x1, y1, widthOfPixel) {
  const mainCanvas = document.getElementById('main-canvas');
  const ctx = mainCanvas.getContext('2d');
  const src = ctx.getImageData(x, y, 1, 1);

  if (x + widthOfPixel > x1 && y + widthOfPixel > y1) {
    let [r, g, b, alfa] = src.data;
    if (r < 256 && g < 256 && b < 256) {
      r += 10;
      g += 10;
      b += 10;
    }
    ctx.fillStyle = `rgba(${r}, ${g}, ${b}, ${alfa})`;
    ctx.clearRect(x, y, widthOfPixel, widthOfPixel);
    ctx.fillRect(x, y, widthOfPixel, widthOfPixel);
  }
}
