import DrawLine from '../../../components/toolbar-list/bresenhamForLine';
import PaintBucket from '../../../components/toolbar-list/bucket';
import eraser from '../../../components/toolbar-list/eraser';
import colorPicker from '../../../components/toolbar-list/color-picker';
import lighten from '../../../components/toolbar-list/lighten';
import dithering from '../../../components/toolbar-list/dithering';

export default class AppView {

  pixel(x, y, data) {
    const mainCanvas = document.querySelector('#main-canvas');
    const widthOfCanvas = mainCanvas.width;
    const ctx = mainCanvas.getContext('2d');
    const pixel = document.querySelector('.pixel');
    const widthOfPixel = parseInt(getComputedStyle(pixel).width);
    const primaryColor = getComputedStyle(document.querySelector('.primary-color')).backgroundColor;
    ctx.fillStyle = primaryColor;
    ctx.beginPath();
    if (data.tool === 'pen') {
      ctx.fillRect(x, y, widthOfPixel, widthOfPixel);
    }
    if (data.tool === 'mirror-pen') {
      let mirrorX = widthOfCanvas - widthOfPixel - x;
      ctx.fillRect(x, y, widthOfPixel, widthOfPixel);
      ctx.fillRect(mirrorX, y, widthOfPixel, widthOfPixel);
    }
    if (data.tool === 'stroke') {
      if (data.event === 'mousedown') {
        ctx.fillRect(data.staticX, data.staticY, widthOfPixel, widthOfPixel);
      } else {
        ctx.fillRect(x, y, widthOfPixel, widthOfPixel);
        data.strokeData.push({x, y, widthOfPixel});
      }
    }
  }

  render(data) {
    const mainCanvas = document.querySelector('#main-canvas');
    const ctx = mainCanvas.getContext('2d');
    const imageData = ctx.getImageData(0, 0, mainCanvas.width, mainCanvas.height);

    if (data.tool === 'eraser') {
      eraser(data.x, data.y, data.pixelData.width);
    }
    if (data.tool === 'paint-bucket' || data.tool === 'colorswap') {
      new PaintBucket().dataForBucket(data.x, data.y, data.pixelData.data, imageData, data.tool);
    }
    if (data.tool === 'color-picker') {
      colorPicker(data.x, data.y);
    }
    if (data.tool === 'lighten') {
      lighten(data.x, data.y, data.absoluteX, data.absoluteY, data.pixelData.width);
    }
    if (data.tool === 'dithering') {
      dithering(data.x, data.y, data.pixelData.width);
    }
    if (data.tool === 'stroke') {
      ctx.clearRect(0, 0, mainCanvas.width, mainCanvas.height);
      DrawLine(data.staticX, data.staticY, data.x1, data.y1, data.pixelData.width, data);
      ctx.putImageData(data.canvasData, 0, 0);
      data.strokeData.forEach((pixel) => {
        ctx.fillRect(pixel.x, pixel.y, pixel.widthOfPixel, pixel.widthOfPixel);
      });
      data.strokeData = [];
    }
    if (data.tool === 'pen' || data.tool === 'mirror-pen') {
      if (data.event === 'mousedown') {
        this.pixel(data.x, data.y, data);
      }
      else {
        DrawLine(data.x, data.y, data.x1, data.y1, data.pixelData.width, data);
      }
    }
  }

}
