import render from './render';

export default function dublicateFrame (elem, number, frameObj) {
  const frameBarWindow = document.querySelectorAll('.framebar-window');
  const frameBar = document.querySelector('.framebar');
  const layerFrame = document.querySelectorAll('.frame-layer');
  const mainCanvas = document.getElementById('main-canvas');
  const wrapperAddFrame = document.querySelector('.framebar__nav');
  const numberOfFrames = document.querySelector('.active-frames');
  const frameCanvasActive = document.querySelectorAll('.framebar-canvas')[number];
  const frameCanvasActiveCtx = frameCanvasActive.getContext('2d');
  const frameCanvasActiveSrc = frameCanvasActiveCtx.getImageData(0, 0, frameCanvasActive.width, frameCanvasActive.height);
  let length = layerFrame.length;
  let count = 0;
  let copyFrame, frame, frameCtx;

  for (let i = 0; i < length; i += 1) {
    if (frameBarWindow[i].classList.contains('framebar-active')) {
      frameBarWindow[i].classList.remove('framebar-active');
    }
  }
  for (let i = 0; i <= length; i += 1) {

    if (i === number && count === 0) {
      elem.classList.remove('framebar-active');
      copyFrame = layerFrame[number].cloneNode(true);
      frame = copyFrame.firstElementChild.children[0];
      frameCtx = frame.getContext('2d');
      frameCtx.putImageData(frameCanvasActiveSrc, 0, 0);
      copyFrame.firstElementChild.classList.add('framebar-active');
      if (number === length - 1) {
        wrapperAddFrame.appendChild(copyFrame);
      } else {
        wrapperAddFrame.insertBefore(copyFrame, layerFrame[number + 1]);
      }
      count += 1;
      i = 0;
    }
    frameObj.numberOfFrame = i + 1;
    frameObj.dataNumberOfFrame = i;
    document.querySelectorAll('.framebar-window__number')[i].innerHTML = frameObj.numberOfFrame;
    document.querySelectorAll('.framebar-window')[i].setAttribute('data-frame-number', frameObj.dataNumberOfFrame);
  }
  numberOfFrames.innerHTML = frameObj.numberOfFrame;
  if (wrapperAddFrame.clientHeight > window.innerHeight - 100) {
    frameBar.style.overflow = 'scroll';
  }
  render(frame, mainCanvas);
}
