export default function chooseColor() {
  const toolbarColor = document.querySelector('.toolbar-color');
  const primaryColor = document.querySelector('.primary-color');
  const secondaryColor = document.querySelector('.secondary-color');
  const inputColor = document.querySelector('.choose-color');
  const swapColors = document.querySelector('.swap-colors');

  toolbarColor.addEventListener('click', (e) => {
    if (e.target.classList.contains('primary-color') || e.target.classList.contains('secondary-color')) {
      e.target.classList.add('active-color');
      inputColor.click();
    }
  });

  inputColor.addEventListener('change', () => {
    document.querySelector('.active-color').style.backgroundColor = inputColor.value;
    document.querySelector('.active-color').classList.remove('active-color');
  });

  swapColors.addEventListener('click', () => {
    const primaryBg = getComputedStyle(primaryColor).backgroundColor;
    const secondaryBg = getComputedStyle(secondaryColor).backgroundColor;
    secondaryColor.style.backgroundColor = primaryBg;
    primaryColor.style.backgroundColor = secondaryBg;
  });
}
