import render from './render';

export default function deleteFrame(number, frameObj) {
  const frameBarWindow = document.querySelectorAll('.framebar-window');
  const layerFrame = document.querySelectorAll('.frame-layer');
  const mainCanvas = document.getElementById('main-canvas');
  const wrapperAddFrame = document.querySelector('.framebar__nav');
  const numberOfFrames = document.querySelector('.active-frames');
  const frameBar = document.querySelector('.framebar');
  let frameCanvas = document.querySelector('.framebar-active').firstElementChild;
  let length = layerFrame.length;
  let count = 0;

  for (let i = 0; i < length; i += 1) {
    if (length === 1) {
      render(frameCanvas, mainCanvas);
      break;
    }
    if (frameBarWindow[i].classList.contains('framebar-active')) {
      frameBarWindow[i].classList.remove('framebar-active');
    }
    if (i === number && count === 0) {
      wrapperAddFrame.removeChild(layerFrame[number]);
      wrapperAddFrame.lastElementChild.children[0].classList.add('framebar-active');
      frameCanvas = document.querySelector('.framebar-active').firstElementChild;
      count += 1;
      length -= 1;
      i = 0;
    }
    frameObj.numberOfFrame = i + 1;
    frameObj.dataNumberOfFrame = i;
    document.querySelectorAll('.framebar-window__number')[i].innerHTML = frameObj.numberOfFrame;
    document.querySelectorAll('.framebar-window')[i].setAttribute('data-frame-number', frameObj.dataNumberOfFrame);
  }
  if (wrapperAddFrame.clientHeight < window.innerHeight) {
    frameBar.style.overflow = 'visible';
  }

  document.querySelectorAll('.framebar-window')[document.querySelectorAll('.framebar-window').length - 1].classList.add('framebar-active');
  numberOfFrames.innerHTML = frameObj.numberOfFrame;
  render(frameCanvas, mainCanvas);
}
