export default function addFrame(frameObj) {
  const addFrameButton = document.querySelector('.framebar-window__add');
  const wrapperAddFrame = document.querySelector('.framebar__nav');
  const frameBar = document.querySelector('.framebar');
  const mainCanvas = document.getElementById('main-canvas');
  const numberOfFrames = document.querySelector('.active-frames');
  const mainCanvasCtx = mainCanvas.getContext('2d');

  addFrameButton.addEventListener('click', () => {
    const frameBarWindows = document.querySelectorAll('.framebar-window');
    const frameBarActive = document.querySelector('.framebar-active');
    for (let i = 0; i < frameBarWindows.length; i += 1) {
      if (frameBarWindows[i].classList.contains('framebar-active')) {
        frameBarWindows[i].classList.remove('framebar-active');
      }
    }
    frameObj.numberOfFrame += 1;
    frameObj.dataNumberOfFrame += 1;
    const layerFrame = document.createElement('li');
    const copyFrame = frameBarActive.cloneNode(true);

    layerFrame.setAttribute('class', 'frame-layer');
    copyFrame.classList.add('framebar-active');
    copyFrame.setAttribute('data-frame-number', frameObj.dataNumberOfFrame);
    copyFrame.children[0].setAttribute('data-frame-number', frameObj.dataNumberOfFrame);
    copyFrame.children[1].innerHTML = frameObj.numberOfFrame;
    layerFrame.appendChild(copyFrame);
    wrapperAddFrame.appendChild(layerFrame);
    mainCanvasCtx.clearRect(0, 0, mainCanvas.width, mainCanvas.height);

    if (wrapperAddFrame.clientHeight > window.innerHeight - 100) {
      frameBar.style.overflow = 'scroll';
    }
    numberOfFrames.innerHTML = frameObj.numberOfFrame;
  });
}
