export default function activeColor() {
  const activeColor = getComputedStyle(document.querySelector('.primary-color')).backgroundColor;
  let str = '';
  for (let i = 4; i < activeColor.length - 1; i += 1) {
    if (activeColor[i] !== ',') {
      str += activeColor[i];
    }
  }
  const fillColor = str.split(' ');
  return fillColor;
}
