export default function render(from, to) {
  const toCtx = to.getContext('2d');
  const fromCtx = from.getContext('2d');
  const ratioX = from.width / to.width;
  const ratioY = from.height / to.height;
  const fromSrc = fromCtx.getImageData(0, 0, from.width, from.height);
  let r, g, b, alfa, x, y;

  for (let i = 0; i < fromSrc.data.length; i += 4) {
    x = Math.floor((i / 4) % from.width / ratioX);
    if (ratioY > 1) {
      y = Math.floor((i / 4) / from.width / ratioY);
    } else {
      y = Math.floor((i / 4) / from.height) * 1 / ratioY;
    }
    r = fromSrc.data[i];
    g = fromSrc.data[i + 1];
    b = fromSrc.data[i + 2];
    alfa = fromSrc.data[i + 3];
    toCtx.beginPath();
    // For Dithering
    if (r === 0 && g === 0 && b === 0 && alfa === 0) {
      if (ratioX > 1) {
        toCtx.clearRect(x, y, 1, 1);
      } else {
        toCtx.clearRect(x, y, 1 / ratioX, 1 / ratioY);
      }
    } else {
      toCtx.fillStyle = `rgba(${r}, ${g}, ${b}, ${alfa})`;
      if (ratioX > 1) {
        toCtx.fillRect(x, y, 1, 1);
      } else {
        toCtx.fillRect(x, y, 1 / ratioX, 1 / ratioY);
      }
    }
  }
  return to;
}
