import AppView from '../../screens/canvas/modules/view';

export default function DrawLine(x1, y1, x2, y2, step, data) {
  let x, y, dx, dy, dx1, dy1, px, py, xe, ye, i;
  const view = new AppView();
  dx = x2 - x1;
  dy = y2 - y1;
  dx1 = Math.abs(dx);
  dy1 = Math.abs(dy);
  px = 2 * dy1 - dx1;
  py = 2 * dx1 - dy1;
  if (dy1 <= dx1) {
    if (dx >= 0) {
      x = x1; y = y1; xe = x2;
    } else {
      x = x2; y = y2; xe = x1;
    }
    view.pixel(x, y, data);
    for (i = 0; x < xe; i += 1) {
      x += step;
      if (px < 0) {
        px += 2 * dy1;
      } else {
        if ((dx < 0 && dy < 0) || (dx > 0 && dy > 0)) {
          y += step;
        } else {
          y -= step;
        }
        px += 2 * (dy1 - dx1);
      }
      view.pixel(x, y, data);
    }
  } else {
    if (dy >= 0) {
      x = x1; y = y1; ye = y2;
    } else {
      x = x2; y = y2; ye = y1;
    }
    view.pixel(x, y, data);
    for (i = 0; y < ye; i += 1) {
      y += step;
      if (py <= 0) {
        py += 2 * dx1;
      } else {
        if ((dx < 0 && dy < 0) || (dx > 0 && dy > 0)) {
          x += step;
        } else {
          x -= step;
        }
        py += 2 * (dx1 - dy1);
      }
      view.pixel(x, y, data);
    }
  }
}
